.App {
  text-align: center;
}

h1, h4 {
  color: #fffce6 !important;
}

h4 {
  text-align: left;
}

.DateSearchDiv{
  margin: 10px;
}

.datePickerSearch {
  margin: 10px;
}

.dateSearchSelect{
  color: #fffce6;
  margin-bottom: 4px;
}

.ant-input-search {
  padding: 1vmin;
}

.ant-card{
  padding: 1vmin;
  margin: 1vmin;
}

.ant-layout-sider-children h1{
  margin-top: 36px;
}

