.ant-menu-dark.ant-menu-dark:not(.ant-menu-horizontal) .ant-menu-item-selected {
  background-color: #eeb113;
}

::selection {
  background: #eeb113;
  background-color: #eeb113;
}

html {
  --antd-wave-shadow-color: #eeb113;
}

.ant-input-search .ant-input:hover, .ant-input-search .ant-input:focus {
  border-color: #eeb113;
}

.ant-btn-primary {
  background: #eeb113;
  border-color: #eeb113;
}

.ant-btn-primary:hover, .ant-btn-primary:focus {
  color: #fff;
  background: #FFCB2D;
  border-color: #FFCB2D;
}

.ant-picker-cell-in-view.ant-picker-cell-selected .ant-picker-cell-inner, .ant-picker-cell-in-view.ant-picker-cell-range-start .ant-picker-cell-inner, .ant-picker-cell-in-view.ant-picker-cell-range-end .ant-picker-cell-inner {
  color: #fff;
  background: #eeb113;
}

.ant-picker-cell-in-view.ant-picker-cell-today .ant-picker-cell-inner::before {
  border: 1px solid #eeb113;
}

.ant-select-single.ant-select-open .ant-select-selection-item {
  color: #eeb113;
}

.dateSearchSelect {
  color: #eeb113;
}

.ant-select-item-option-selected:not(.ant-select-item-option-disabled) {
  background-color: #FFFFE5;
}

.ant-picker-input > input:focus, .ant-picker-input > input-focused {
  border-color: #FFCB2D;
}

.ant-picker:hover, .ant-picker-focused {
  border-color: #FFCB2D;
}

.ant-picker-today-btn {
  color: #eeb113;
}

.ant-picker-today-btn:hover {
  color: #FFCB2D;
}

.ant-btn-circle:hover, .ant-btn-circle:focus {
  border-color: #FFCB2D;
  color: #FFCB2D;
}

.ant-layout{
  background-color: #FbFbFe;
}

.clear-button{
  color: #eeb113;
}

.clear-button:hover{
  color: #FFCB2D;
}

.ant-slider{
  margin-left: 20px;
  margin-right: 20px;
}

.ant-slider-mark-text{
  color: #fff;
}

.ant-slider-track {
  background-color: #eeb113;
}

.ant-slider:hover .ant-slider-track {
  background-color: #FFCB2D;
}

/* Article CSS */
.ant-col h3{
  text-align: left;
}

.ant-card-body p{
  text-align: left;
}

p.card-date {
 margin-bottom: auto;
}

p.results-num {
  margin: auto;
  color: #d29c11;
}

.ant-layout-sider{
  background: #000;
}

.ant-menu.ant-menu-dark{
  background: #000;
}

.ant-layout-sider-zero-width-trigger ant-layout-sider-zero-width-trigger-leftsider{
  background: #000;
}